.home {
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  animation: homePage 1.5s linear;

  @keyframes homePage {
    0% {
     
      opacity: 0;
    }
    100%{
     opacity: 100%;
    }
   
   
  }

}

.notHome{
  display: none;
}
